/// app 1

.app1 {
  padding: 6.25rem 0px;

  &__form {
    box-shadow: 0px 2px 1px -1px rgba(0, 0, 0, 20%),
      0px 1px 1px 0px rgba(0, 0, 0, 14%), 0px 1px 3px 0px rgba(0, 0, 0, 12%);
    margin: 3.125rem 0px;
    display: flex;
    flex-direction: column;
    gap: 30px;
    align-items: center;
    padding: 50px;
    border-radius: 30px;
  }

  &__form-el {
    width: 50%;
  }
}
