// LeftChapter
.center {
  padding: 6.25rem 0px;
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */

    letter-spacing: 0.02em;
    margin-bottom: 1.875rem;
    color: $color-grin;
  }
  &__blog-2 {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 15px;
  }
  &__item {
    width: 100%;
    height: auto;
    border-radius: 8px;
    transition: 0.3s all ease;
    display: flex;
    flex-direction: column;
    padding: 30px;
    border: 2px solid rgba(47%, 52%, 49%, 0.1);
    box-shadow: 4px 4px 18px 0px rgba(120, 132, 125, 0.2);
    h2 {
      margin-top: 1.25rem;

      font-size: 1rem;
      font-weight: 400;
      line-height: 32px;
      letter-spacing: 0em;
      text-align: left;
    }
  }

  &__item:hover {
    z-index: 10;
    transition: 0.3s all ease;
    transform: scale(1.2);
    border: 2px solid rgba(47%, 52%, 49%, 0.1);
    box-shadow: 4px 9px 30px 23px rgba(120, 132, 125, 0.2);
    background-color: white;
  }
}

@media (max-width: 1024px) {
  .center {
    &__blog-2 {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media (max-width: 640px) {
  .center {
    &__blog-2 {
      display: grid;
      grid-template-columns: 1fr;
    }
  }
}

// RightChapter
.right {
  padding: 6.25rem 0px;
  &___content {
    display: grid;
    grid-template-columns: auto 60%;
    align-items: center;
    justify-content: space-between;
  }
  &___blog-2 {
    height: 400px;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
    }
  }
  &___title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */

    letter-spacing: 0.02em;

    color: $color-grin;
  }
  &___list {
    display: flex;
    flex-direction: column;
    margin-top: 10px;
  }
  &___item {
    font-style: normal;
    font-weight: 400;
    font-size: 1.375rem;
    line-height: 40px;
    /* or 182% */
    color: $color-gray;
    position: relative;
    padding-left: 30px;
  }
  &___item::before {
    content: '';
    position: absolute;
    display: block;
    top: 50%;
    left: 5px;
    transform: translate(0, -50%);
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: $color-gray;
  }
}

@media (max-width: 640px) {
  .right {
    padding: 6.25rem 0px;
    &___content {
      display: grid;
      grid-template-columns: 1fr;
      align-items: center;
      justify-content: center;
    }
    &___blog-2 {
      display: none;
    }
    &___title {
      text-align: center;
    }
    &___list {
    }
    &___item {
      padding-left: 100px;
    }
    &___item::before {
      left: 70px;
    }
  }
}
@media (max-width: 437px) {
  .right {
    padding: 6.25rem 0px;
    &___content {
    }
    &___blog-2 {
    }
    &___title {
      text-align: center;
    }
    &___list {
    }
    &___item {
      padding-left: 23px;
    }
    &___item::before {
      left: 5px;
    }
  }
}

//Slider

.sliderChapter {
  padding: 6.25rem 0px;
  &__title {
    font-style: normal;
    font-weight: 700;
    font-size: 2.625rem;
    line-height: 65px;
    /* or 155% */
    text-align: center;
    letter-spacing: 0.02em;
    margin-bottom: 1.875rem;
    color: $color-grin;
  }

  .swiper-slide {
    height: auto;
    box-sizing: border-box;
    border-radius: 5px;
    display: flex;
    min-height: 400px;
    flex-direction: column;
    border: 2px solid rgba(47%, 52%, 49%, 0.1);

    .slider__img {
      width: 100%;
      display: block;
      height: 200px;
      position: relative;
      img {
        border-radius: 5px 5px 0px 0px;
        object-fit: cover;
        width: 100%;
        height: 100%;
      }
    }

    .slider__text {
      padding: 10px;
    }
  }
}

/// title

.hero {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin-top: -75px;
  height: 852px;
  padding: 75px 0px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  &__subtitle {
    font-style: normal;
    font-weight: 500;
    font-size: 2.25rem;
    text-align: center;
    letter-spacing: 0.02em;
    color: #ffffff;
    margin-bottom: 1.875rem;
  }
  &__title {
    font-style: normal;
    font-weight: 600;
    font-size: 5.625rem;
    text-align: center;
    /* identical to box height */
    letter-spacing: 0.04em;
    color: #ffffff;
    margin-bottom: 1.875rem;
  }
  &__about {
    font-style: normal;
    font-weight: 400;
    font-size: 1.375rem;
    line-height: 40px;
    text-align: center;
    margin-bottom: 1.875rem;

    color: #ffffff;

    opacity: 0.6;
  }

  &__button-text {
    font-style: normal;
    font-weight: 600;
    font-size: 1.25rem;
    line-height: 24px;
    /* identical to box height */

    letter-spacing: 0.02em;

    color: #ffffff;

    text-shadow: 0px 4px 20px rgba(0, 0, 0, 0.25);
  }
}

@media (max-width: 1024px) {
  .hero {
    &__subtitle {
      font-size: 2rem;
    }
    &__title {
      font-size: 3.625rem;
    }
    &__about {
    }

    &__button-text {
    }
  }
}

@media (max-width: 640px) {
  .hero {
    &__subtitle {
      font-size: 1.7rem;
    }
    &__title {
      font-size: 2.425rem;
    }
    &__about {
      display: none;
    }

    &__button-text {
    }
  }
}

/// footer

.footer {
  padding: 4.125rem 0px;

  &__line {
    width: 100%;
    background-color: rgba(20, 24, 62, 0.15);
    height: 1px;
  }
  &__title {
    font-size: 1.3rem;
    font-weight: 700;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 30px;
    color: $color-grin;
    margin-bottom: 3rem;
  }
  &__title-info {
    font-size: 1rem;
    font-weight: 400;
    line-height: 34px;
    letter-spacing: 0em;
    text-align: left;
    margin-top: 30px;
    color: $color-grin;
  }
}

// header

.header {
  &__block {
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__title {
    font-size: 1.625rem;
    font-weight: 600;
    line-height: 31px;
    letter-spacing: 0.02em;
    text-align: left;
    margin-right: 10px;
  }
  &__icon {
    height: 19px;
    width: 19px;
    svg {
      width: 100%;
      height: 100%;
      fill: white;
    }
  }
  &__button:last-child {
    margin: 0;
  }
  &__menu-2 {
    display: none;
  }
}

@media (max-width: 1024px) {
  .header {
    &__block {
    }
    &__title {
      font-size: 1.2rem;
      font-weight: 600;
      line-height: 31px;
      letter-spacing: 0.02em;
      text-align: left;
      margin-right: 0.625rem;
    }
    &__icon {
    }
    &__button:last-child {
      margin: 0;
    }
  }
}
@media (max-width: 640px) {
  .header {
    &__block {
    }
    &__title {
      font-size: 1.2rem;
      font-weight: 600;
      line-height: 31px;
      letter-spacing: 0.02em;
      text-align: left;
      margin-right: 10px;
      display: none;
    }
    &__icon {
      height: 30px;
      width: 30px;
      svg {
        width: 100%;
        height: 100%;
        fill: white;
      }
    }
    &__button:last-child {
      margin: 0;
    }
    &__menu-1 {
      display: none;
    }
    &__menu-2 {
      display: block;
    }
  }
}

// info

.info {
  padding: 6.25rem 0px;
  &__block {
    display: grid;
    grid-template-columns: 400px 1fr;
    align-items: center;
  }

  @media (max-width: 1024px) {
    .info {
      &__block {
        display: grid;
        grid-template-columns: 1fr;
      }
      &__title {
        margin-bottom: 1.25rem;
      }
    }
  }

  @media (max-width: 640px) {
    .info {
      &__title {
        text-align: center;
      }
    }
  }
}

//loading

.loading {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 100;
  top: 0;
  left: 0;
  background-color: aliceblue;
  display: flex;
  align-items: center;
  justify-content: center;
}
